.modal {
    min-height: 300px;
    width: 720px;
    max-width: 100%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #1C1C21;
    border: 1px solid var(--border-color);
    border-radius: 40px;
    box-shadow: 0 24px 32px rgba(0, 0, 0, 0.04), 0 16px 24px rgba(0, 0, 0, 0.04),
      0 4px 8px rgba(0, 0, 0, 0.04), 0 0 1px rgba(0, 0, 0, 0.04);

    z-index: 999;
  }
  
  .modalCloseIcon {
    cursor: pointer;
    position: absolute;
    top: 16px;
    right: 24px;
  }